import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

//getter
const state = {
    rendered: 0,
    item: {},//当前文件信息
    dir: "", //当前目录
    token: null,
};

//setter
const mutations = {
    receiveItem(state, payload) {
        state.item = payload;
    },
    receiveDir(state, payload) {
        state.dir = payload;
    },
    receiveToken(state, payload) {
        if(payload){
            sessionStorage.setItem("access_token", payload);
        }
        else{
            sessionStorage.removeItem("access_token");
        }
        state.token = payload;
    },
    forceRender(state, payload) {
        state.rendered ++;
    }
};

const actions = {
};

export default new Vuex.Store({
    state,
    mutations,
    actions
})
