<template>
    <div class="fluid-container">
        <div class="panel entry-list">
            <ul class="entry-box">
                <li class="entry" v-on:click="parentDir()" v-bind:class="{ active: loading && items === false, disabled: dir === '' }">
                    <div class="entry-body">
                        <div class="entry-indicator" v-if="loading && items === false"><Loading/></div>
                        返回上层
                    </div>
                    <div class="entry-footer">
                        {{dir || "..."}}</div>
                </li>
                <li class="entry" v-for="it in items" v-on:click="processItem(it)" v-bind:class="{ active: it.path === item.path }">

                    <div class="entry-body">
                        <div class="entry-indicator" v-if="loading && it.path === item.path"><Loading/></div>
                        {{it.name}}
                    </div>
                    <div class="entry-footer" v-if="it.type === 'file'">{{it.size}} byte</div>
                    <div class="entry-footer" v-if="it.type === 'dir'">目录</div>
                </li>
            </ul>
        </div>
        <div class="panel markdown-body">
            <div v-html="preview"></div>
        </div>


    </div>
</template>

<script>

    import Loading from './Loading.vue';
    import GithubApi from '../github';
    import { mapMutations, mapState } from 'vuex'

    export default {
        name: 'Entry',
        components: {
            Loading
        },
        data() {
            return {
                loading: 0,
                items: false,
                preview: null
            }
        },
        computed: {
            ...mapState([
                'dir',
                'item'
            ])
        },
        created() {
        },
        beforeDestroy() {
        },
        mounted() {
            let item = {
                "type": "dir",
                "path": ""
            };

            let token = this.$route.query.access_token;

            if(!token){
                token = sessionStorage.getItem("access_token")
            }

            if(token){
                this.receiveToken(token);
            }

            //读取列表/文件+ hash定位
            let prefix = this.$route.params.prefix;
            let hash = this.$route.params.hash;
            if(prefix && hash) {
                item.type = GithubApi.contentType(prefix);
                item.path = GithubApi.decode(hash);
            }

            this.processItem(item).then(promise => {
                if(this.$route.hash){
                    let anchor = document.querySelector("[href='" + decodeURIComponent(this.$route.hash) + "']");
                    if(anchor){
                        let headerOffset = 50;
                        let elementPosition = anchor.getBoundingClientRect().top;
                        let offsetPosition = elementPosition - headerOffset;

                        window.scrollTo({
                            top: offsetPosition,
                            behavior: "smooth"
                        });
                    }
                }

            });

            //若是文件恢复列表
            if(item.type === 'file'){
                this.loading ++;
                let path = this.findDir(item.path);
                this.receiveDir(path);
                return this.showDir(path).then(promise => {
                    this.receiveItem(this.items.find(it =>{
                        return it.path === item.path;
                    }));
                    return promise;
                }).finally(() =>{
                    this.loading--;
                });
            }
        },
        methods: {
            ...mapMutations([
                'receiveItem',
                'receiveDir',
                'receiveToken'
            ]),
            findDir(path){
                return path.replace(/\/?[^/]*$/, "");
            },
            parentDir(){
                //待优化
                let matches = this.dir.match("[^/]+(/[^/]*)?$");
                if(!matches) {
                    return ; //已在頂層
                }
                this.processItem({
                    type: 'dir',
                    path: this.dir.slice(0, this.dir.length - matches[0].length)
                });
            },
            processItem(it){
                if(this.loading){
                    return ;
                }
                this.loading++;

                let location = GithubApi.location(it.type, it.path);
                if(location !== this.$route.path){
                    this.$router.replace(location);
                }

                if(it.type === 'dir'){
                    this.receiveItem({});
                    this.receiveDir(it.path);
                    return this.showDir(it.path).finally(() =>{
                            this.loading--;
                        });
                }

                this.receiveItem(it);
                return this.showFile(it.path).finally(() =>{
                        this.loading--;
                    });
            },
            showDir(path) {
                this.items = false;
                return GithubApi.contents(path).then(promise => {
                    this.items = promise.data;
                    return promise;
                })
            },
            showFile(path) {
                return GithubApi.contents(path).then(promise => {
                    this.preview = promise.data;
                    return promise;
                });
            }
        }
    }

</script>



<style scoped>
    @import '~github-markdown-css/github-markdown.css';

    .entry-list {
        flex: 1;
        margin-right: 1rem;
    }

    .markdown-body {
        flex: 3;
        box-sizing: border-box;
        padding: 1rem;
        overflow: hidden;
    }

    @media screen and (max-width: 767px) {
        .fluid-container {
            flex-direction: column-reverse;
        }

        .markdown-body {
            width: 100%;
        }

        .entry-list {
            width: 100%;
            margin-right: 0;
            margin-top: 1rem;
        }
    }

    .entry {
        position: relative;
        padding: 0.5rem 1rem;
    }

    .entry:not(:last-child) {
        border-bottom: 1px solid #f1f1f1;
    }

    .entry.active .entry-body {
        color: #7D2727;
    }

    .entry.disabled:hover {
        cursor: not-allowed;
        background-color: #fff;
    }

    .entry.disabled .entry-body {
        color: #bfbfbf;
    }

    .entry:hover {
        background-color: #f9f9f9;
        cursor: pointer;
    }

    .entry-indicator {
        position: absolute;
        right: 1rem;

    }

    .entry-body {
        font-size: 1rem;
        font-weight: bolder;
        color: #0366d6;
    }

    .entry-footer {
        font-size: 0.5rem;
        color: #b2bac2;
        text-align: right;
    }

    .panel {
        background: #fff;
        border-radius: 2px;
    }


</style>
