<template>
    <div class="header">
        <div class="fluid-container">
            <div class="logo">MarkdownBook</div>
            <div class="nav">
                <ul>
                    <template v-if="login === github.owner">
                        <template v-if="$route.name === 'editor'">
                            <li v-if="!item.path"><label>
                                <input type="search" placeholder="文件名称" v-model="path" />
                                .md
                            </label></li>
                            <li><a v-bind:class="{ disabled: !!processing, blink: processing === 'save'}" href="javascript:void(0)" v-on:click="saveItem">保存</a> </li>
                            <li><a v-bind:class="{ disabled: !!processing }" href="javascript:void(0)" v-on:click="discardItem">返回</a> </li>
                        </template>

                        <template v-else>
                            <li><a v-bind:class="{ disabled: !!processing }" href="javascript:void(0)" v-on:click="newItem">新增</a> </li>

                            <template v-if="item.path">
                                <li><a v-bind:class="{ disabled: !!processing }" href="javascript:void(0)" v-on:click="editItem">修改</a> </li>
                                <li><a v-bind:class="{ disabled: !!processing, blink: processing === 'remove'}" href="javascript:void(0)" v-on:click="removeItem">删除</a> </li>
                            </template>
                        </template>
                    </template>
                    <li>
                        <img v-if="login" class="avatar" v-bind:title="'@' + login + ' 登出'" v-bind:alt="'@' + login" v-bind:src="avatar" v-on:click="logout()">
                        <a v-else-if="!token" href="javascript:void(0)" @click.exact="goLogin(false)" @click.ctrl.exact="goLogin(true)">登录</a>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import GithubApi from '../github';
    import { mapMutations, mapState } from 'vuex'

    export default {
        name: "Header",
        computed: {
            ...mapState([
                'item',
                'dir',
                'token'
            ]),
        },
        data(){
            return {
                processing: false,
                path: null,
                login: null,
                avatar: null,
                github: GithubApi.config
            }
        },
        mounted(){
            this.updateUser();
        },
        watch: {
            path(newValue, prevValue){
                if(!/^\w*(\w+\/?){0,2}\w*$/.test(newValue)){
                    this.$nextTick(() => {
                        this.path = prevValue;
                    });
                }
            },
            token(newValue, prevValue){
                this.updateUser();
            },
            dir(newValue, prevValue){
                this.path = newValue;
            },
        },
        methods: {
            ...mapMutations([
                "receiveItem",
                "receiveToken",
                "forceRender"
            ]),
            updateUser(){
                if(this.token){
                    GithubApi.user().then(promise => {
                        this.login = promise.data.login;
                        this.avatar = promise.data.avatar_url + "&s=32";
                    })
                }
                else{
                    this.login = null;
                    this.avatar = null;
                }
            },
            goHome(callback){
                if(this.item.path && this.item.type === 'file'){
                    return this.$router.push(GithubApi.location(this.item.type, this.item.path), callback);
                }
                return this.$router.push(GithubApi.location("dir", this.dir), callback);
            },
            goLogin(me){
                return this.$router.push({
                    name: "login",
                    query: {
                        me: me,
                        redirectUrl: window.location.href
                    }
                });
            },
            logout(){
                this.receiveToken(null); //清除sessionStorage vuexStore
                GithubApi.config.client = null; //清除全局变量
            },
            newItem() {
                this.receiveItem({});
                return this.$router.push("/editor")
            },
            editItem() {
                return this.$router.push("/editor")
            },
            removeItem() {
                if(this.processing){
                    return;
                }
                this.processing = "remove";
                GithubApi.remove(this.item).then(promise => {
                    this.receiveItem({});
                    return this.goHome(() =>{
                        this.forceRender();
                    });
                }).finally(() => {
                    this.processing = false;
                });
            },
            saveItem(){
                if(this.processing){
                    return;
                }
                this.processing = "save";
                if(!this.item.path) {
                    this.item.path = this.path + '.md';
                }
                GithubApi.commit(this.item).then(promise => {
                    this.receiveItem(promise.data.content);
                    return this.goHome();
                }).finally(() => {
                    this.processing = false;
                });
            },
            discardItem(){
                this.goHome();
            }
        }
    }
</script>

<style scoped>
    .header {
        position: fixed;
        left: 0;
        top: 0;
        background: #fff;
        border-bottom: 1px solid #f1f1f1;
        color: #0366d6;
        height: 3rem;
        width: 100%;
        z-index: 100;
    }

    .nav {
        margin-top: -.5rem;
    }

    .nav ul {
        display: flex;
        justify-content: flex-end;
        align-items: start;
        line-height: 2rem;
    }

    .nav ul li {
        position: relative;
        cursor: pointer;
        padding: 0 1rem;
        font-size: 1rem;
        border-right: 1px solid hsla(0,0%,59.2%,.2);
    }

    .nav ul li a{
        color: #0366d6;
        text-decoration: none;
    }

    .logo {
        margin-right: 1rem;
        flex: 1;
        font-weight: bolder;
        font-size: 1rem;
    }

    .nav {
        flex: 3;
    }

    @keyframes blinker {
        50% { opacity: 0; }
    }

    .nav ul li a.disabled {
        pointer-events: none;
    }

    .nav ul li a.blink {
        animation: blinker 1s linear infinite;
    }

    .nav ul li input {
        line-height: 2rem;
        padding: 0 .5rem;
        border: 1px solid #e6edf4;
        border-radius: 2px;
        background-color: #f1f5fa;
        color: #0366d6;
    }

    .nav ul li input:focus {
        outline: none;
        border-color: #007fff;
        background-color: #fff;
    }

    .nav .avatar{
        cursor: pointer;
        vertical-align:middle;
        height: 1rem;
        width: 1rem;
    }

</style>
