import {Base64} from 'js-base64';
import Octokit from "@octokit/rest/index"

const publicToken = "040ab84ddddee4c947c9f0f23a546464045f01ad";

const github = {
    owner: "pnparadise",
    repo: "gitbook",
    publicToken: publicToken,
    client: null,
    defaultClient: new Octokit({
        auth: "token " + publicToken //public only
    })
};

function prepare() {
    if (!github.client) {
        let token = sessionStorage.getItem("access_token");
        if (token) {
            github.client = new Octokit({
                auth: 'token ' + token
            });
        }
    }

    return new Promise((resolve, reject) => {
        if (github.client) {
            resolve(github.client);
        } else {
            resolve(github.defaultClient);
        }
    });
}


export default {
    config: github,
    contents(path, format) {
        return prepare().then(client => {
            return client.repos.getContents({
                "owner": github.owner,
                "repo": github.repo,
                "path": path,
                "headers": {
                    "If-None-Match": ''
                },
                "mediaType": {
                    'format': format || 'html'
                }
            });
        });
    },
    remove(item) {
        return prepare().then(client => {
            return client.repos.deleteFile({
                "owner": github.owner,
                "repo": github.repo,
                "path": item.path,
                "message": "octokit: [master] page deleted",
                "sha": item.sha
            });
        });
    },
    commit(item) {
        return prepare().then(client => {
            return client.repos.createOrUpdateFile({
                "owner": github.owner,
                "repo": github.repo,
                "path": item.path,
                "message": "octokit: [master] page modified",
                "content": Base64.encode(item.text),
                "sha": item.sha
            });
        });
    },
    user() {
        return prepare().then(client => {
            return client.users.getAuthenticated();
        })
    },

    grants(){
        return prepare().then(client => {
            return client.oauthAuthorizations.listGrants()
        })

    },

    /**
     * encrypto 加密程序
     * @param {String} str 待加密字符串
     * @return {String} 加密后的字符串
     */
    encode(str) {
        return Base64.encodeURI(str);
    },

    /**
     * decrypto 解密程序
     * @param {String} str 待加密字符串
     * @return {String} 加密后的字符串
     */
    decode(str) {
        return Base64.decode(str);
    },

    contentType(prefix) {
        return prefix === 'd' && "dir" || "file";
    },

    location(type, path) {
        return "/" + type.charAt(0) + "/" + this.encode(path);
    }


}


