import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue';
import Editor from './views/Editor.vue';
import Entry from './views/Entry.vue';
import Login from './views/Login.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          name: 'home',
          path: '/',
          component: Entry
        },
        {
          path: '/editor',
          component: Editor,
          name: 'editor'
        },
        {
          name: 'entry',
          path: '/:prefix([f|d]?)/:hash?',
          component: Entry
        },
        {
          name: 'login',
          path: '/login',
          component: Login
        }
      ]
    }
  ]
})
